	module.exports = {"data":{"markdownRemark":{"html":"<p>   This area is dominated by swamplands created by the swollen river Celduin\n(\"The River Running\"). Within these swamplands is a small abandoned fortress.\nThe fortress held strategic importance during the 3rd Age as it dominated the\ncrossroads which marked the intersection of the Southern/Dale Road and the Old\nForest Road. However Easterlings have taken over much of this area now and\nprefer to reside in a newly built fort to the northwest which is not sinking\nslowly into the swamplands. The abandoned fortress was once home to a henchman\nof Sauron, Angmacar the Black. It was abandoned in the latter part of the 3rd\nAge with the death of Angmacar during the  War of the Rings. The body of\nAngmacar and his mysterious slayer were never recovered.  It is rumoured that\nhe was buried beneath his fortress and the burial chamber sealed by magical\nkeys.</p>\n<p>Aeglos, Sage and Scribe of Loth Lanath.</p>","frontmatter":{"title":"The Fortress of Angmacar"}}},"pathContext":{"path":"/books/fortressofangmacar"}}

